"use strict";

function unflattenObj(data) {
  var result = {};
  for (var i in data) {
    var keys = i.split(".");
    keys.reduce(function (r, e, j) {
      return (
        r[e] ||
        (r[e] = isNaN(Number(keys[j + 1]))
          ? keys.length - 1 == j
            ? data[i]
            : {}
          : [])
      );
    }, result);
  }
  return result;
}
const flattenObj = (ob) => {
  let result = {};
  for (const i in ob) {
    if (typeof ob[i] === "object" && !Array.isArray(ob[i])) {
      const temp = flattenObj(ob[i]);
      for (const j in temp) {
        result[i + "." + j] = temp[j];
      }
    } else {
      result[i] = ob[i];
    }
  }
  return result;
};

function PaymentElement(componentType, options) {
  this.componentType = componentType;
  this.options = options;

  this.collapse = () => {
    //do nothing i guess?
    //This method collapses the Payment Element into a row of payment method tabs.
  };

  this.blur = () => {
    //send message to iframe saying to blur the element.
  };

  this.clear = () => { };

  this.update = (newOptions) => {
    let flatOption = flattenObj(options);
    let newFlatOption = flattenObj(newOptions);

    for (const i in flatOption) {
      flatOption[i] =
        newFlatOption[i] !== undefined ? newFlatOption[i] : flattenOption[i];
    }

    for (const i in newFlatOption) {
      if (flatOption[i] === undefined) {
        flatOption[i] = newFlatOption[i];
      }
    }
    return unflattenObj(flatOption);
  };
  this.mount = (selector) => {
    let oElement = document.querySelector(selector);

    if (oElement) {
      oElement.innerHTML = `<iframe src="https://orca-pp.netlify.app/?componentName=${componentType}" style="border: 0px;" width="100%" height="100%"></iframe> `;
    }
  };
}

function Elements(options) {
  this.options = options;
  this.savedPaymentElement = null;

  this.getElement = (componentName) => {
    return this.savedPaymentElement[componentName];
  };

  this.update = (newOptions) => {
    this.options.locale =
      newOptions.locale !== undefined ? newOptions.locale : this.options.locale;
    this.options.appearance =
      newOptions.appearance !== undefined
        ? newOptions.appearance
        : this.options.appearance;
  };

  this.fetchUpdates = () => {
    //API Call from PaymentIntent and SetupIntent over here.
    return new Promise((resolve, reject) => {
      setTimeout(resolve, 1000);
    });
  };
  this.create = (
    componentType,
    newOptions = {
      defaultValues: {
        billingDetails: {
          name: undefined,
          email: undefined,
          phone: undefined,
          address: {
            line1: undefined,
            line2: undefined,
            city: undefined,
            state: undefined,
            country: undefined,
            postal_code: undefined,
          },
        },
        email: undefined, //string for linkAuthentication
      },
      business: {
        name: undefined,
      },
      paymentMethodOrder: undefined, //array
      fields: {
        billingDetails: {
          name: undefined, //'never' | 'auto'|object
          email: undefined,
          phone: undefined,
          address: {
            line1: undefined,
            line2: undefined,
            city: undefined,
            state: undefined,
            country: undefined,
            postal_code: undefined,
          },
        },
      },
      readOnly: undefined,
      terms: {
        auBecsDebit: undefined,
        bancontact: undefined,
        card: undefined,
        ideal: undefined,
        sepaDebit: undefined,
        sofort: undefined,
        usBankAccount: undefined,
      },
      wallets: {
        applePay: undefined,
        googlePay: undefined,
      },
      issuingCard: undefined,
      ephemeralKeySecret: undefined,
      nonce: undefined,
      toCopy: undefined,
      style: undefined, //object
      // for card,
      classes: {
        base: undefined,
        complete: undefined,
        empty: undefined,
        focus: undefined,
        invalid: undefined,
        webkitAutofill: undefined,
      },
      value: undefined,
      hidePostalCode: undefined,
      iconStyle: undefined,
      hideIcon: undefined,
      disabled: undefined,
      placeholder: undefined,
      showIcon: undefined,
      accountHolderType: undefined,
      supportedCountries: undefined,
      placeholderCountry: undefined,
      paymentRequest: undefined, //type paymentRequestButton
      amount: undefined,
      currency: undefined,
      logoColor: undefined,
      fontColor: undefined,
      fontSize: undefined,
      textAlign: undefined,
    }
  ) => {
    this.savedPaymentElement = {
      componentType: new PaymentElement(componentType, newOptions),
    };
    return this.savedPaymentElement[componentType];
  };
}

function StripeInstance(
  publishableKey,
  options = {
    stripeAccount: undefined,
    apiVersion: undefined,
    locale: undefined,
  }
) {
  this.publishableKey = publishableKey;
  this.options = options;
  this.iframeElem = null;
  this.clientSecret = null;

  this.elements = (
    elementOptions = {
      fonts: undefined,
      locale: undefined,
      clientSecret: undefined,
      appearance: undefined,
      loader: undefined,
    }
  ) => {
    return new Elements(elementOptions);
  };

  this.confirmPayment = ({ elements, confirmParams }) => {
    console.log("will be confirming payment and redirect", this.iframeElem);

    return new Promise((resolve, _reject) => {
      if (this.iframeElem) {
        this.iframeElem.contentWindow.postMessage(
          `{"doSubmit": true, "clientSecret":"${this.clientSecret}"}`,
          "*"
        );

        const handleMessage = (ev) => {
          try {
            console.log("Submitting", ev.data)
            const json = ev.data;

            if (typeof json.submitSuccessful === "boolean") {
              // window.removeEventListener("message", handleMessage);
              if (json.submitSuccessful) {
                let url = confirmParams && confirmParams.return_url;

                if (url) {
                  window.location.replace(url);
                }
              } else {
                resolve(json);
              }
            }
          } catch (err) {
            //
          }
        };

        window.addEventListener("message", handleMessage);
      }
    });
  };
}

const loadJuspay = (str) => {
  return Promise.resolve(new StripeInstance(str));
};
const loadStripe = (str) => {
  console.warn("loadStripe is deprecated. Please use loadJuspay instead.");
  return loadJuspay(str);
};

const Stripe = (str) => {
  return new StripeInstance(str);
};

